// если вы хотите использовать sass-переменные - удалите root
//<Шрифт по умолчанию>==========================================================================================
$fontFamily: "Montserrat", sans-serif;
$fontSize: 12;
//<ПЕРЕМЕННЫЕ>==========================================================================================================
$maxWidth: 1920;
$maxWidthContainer: 1130;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

//</ПЕРЕМЕННЫЕ>======================
:root {
  // base
  --font-family: "Open Sans", sans-serif;
  --content-width: 1200px;
  --container-offset: 36px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --font-body-scale: 1.0;
  
  // colors
  --gradient-background: #3a3a3a;
  --light-color: #fff;
  --color-foreground: 248, 233, 214;
  --color-background: 248, 233, 214;
}

