// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "vendor";
@import "settings";
@import "ui";


// подключения компонентов страницы
@import "./components/header";

.tabs {
  @include tabs;
}


.main {
  flex: 1 1 auto;
  // .main_section

  &_section {
    padding: 25px 0;
  }

  // .main_wrapper
  &_wrapper {
    position: relative;
    z-index: 1;
    background-color: rgba(var(--color-background));
  }
}

.heroes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 100px;
  min-height: 100vh;
  color: rgba(var(--color-foreground));

  // .heroes_body
  &_body {
  }

  // .heroes_bg

  &_bg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
  }

  // .heroes_content

  &_content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  // .heroes_text

  &_text {
    font-size: 16px;
    h1,h2,h3,h4 {
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
    p {
      &:not(:last-child){
        margin-bottom: 5px;
      }
    }
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }

  // .heroes_button

  &_button {
  }
}

.in-marvel {

  // .in-marvel_body

  &_body {
  }

  // .in-marvel_cells

  &_cells {
    &:nth-child(2n){
      flex-direction: row-reverse;
    }
  }
}

.cells {
  display: flex;
  align-items: center;
  // .cells_item

  &_item {
    padding: 10px 40px 10px 40px;
  }

  // .cells_item--img

  &_item--img {
    flex: 0 0 50%;
    padding: 0 0 50% 0;
    img {
      border-radius: 6px;
    }
  }
}


